import React from 'react';
import PropTypes from 'prop-types';
import './App.css';

class TextInput extends React.Component {
	constructor(props) {
		super(props);
		this.inputRef = React.createRef();
	}

	componentDidMount() {
		this.inputRef.current.focus();
	}

	tookInput = (e) => {
		console.log(e);
		/*if(e.target.value === this.props.target) {
			this.props.callback();
		}*/
	}

	render() {
		return (
			<label className="text-label">
				{this.props.target}
				<input id='input' type="text" ref={this.inputRef} onChange={this.tookInput} className="text-input"/>
			</label>
		);
	}
}

TextInput.propTypes = {
	target: PropTypes.string,
	callback: PropTypes.func
};

class App extends React.Component {
	constructor(props) {
		super(props);
	}

	reloadTarget = () => {
		console.log("Reloaded");
	}

	render() {
		return (
			<TextInput callback={this.reloadTarget} target="test"/>
		);
	}
}

export default App;
